<template>
  <div class="all-container">
    <topbar/>
    <el-row class="add-tip">
      <i class="linear-line margin-horizontal"/>
      <span class="tip-text">3</span>
    </el-row>
    <!---->
    <div class="contact-panel">
      <el-row class="first-item">
        <i class="el-icon-star-on red-star margin-horizontal"></i>
        <span class="title-name">{{parseInt(this.index)+1}}</span>
        <span class="title-tip">()</span>
      </el-row>
      <el-row class="first-input-container bottom-border">
        <el-input type="number" v-model="number" class="normal-input-style no-border-input" placeholder=""
                  clearable></el-input>
      </el-row>
      <el-row class="second-input-container">
        <el-input type="number" v-model="verifyNumber" class="normal-input-style no-border-input sixty-percent"
                  placeholder=""></el-input>
        <span class="fourty-percent verify-code-text-btn" @click="getVerifyCode">{{this.verifyBtnContent}}</span>
      </el-row>
    </div>
    <el-button type="success" class="wide-button" @click="verify"></el-button>
    <el-dialog
      :visible.sync="centerDialogVisible"
      title=""
      width="80%"
      :show-close="false"
      center>
      <span class="center-dialog-content"></span>
      <span slot="footer" class="dialog-footer dialog-btn-group">
        <span @click="dialogSkip" class="skip-btn"></span>
        <span type="primary" @click="dialogAddMore" class="add-another-btn"></span>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import Topbar from './topbar'
import api from '../api'
export default {
  name: 'addContact',
  components: {Topbar},
  data () {
    return {
      number: '',
      verifyNumber: '',
      verifyBtnContent: '',
      isVerifyBtnClickable: true,
      centerDialogVisible: false,
      isClearInterval: false,
      contact: sessionStorage.getItem('contact') === null ? '' : sessionStorage.getItem('contact'),
      modifyIndex: 1,
      index: this.$route.query.index ? this.$route.query.index : 0
    }
  },
  created () {
    document.title = this.$route.meta.title
  },
  mounted: function () {
    // settings
    var number = this.$route.query.modifyNumber
    console.log('addContact:', number)
    console.log('addContactcontact:', this.contact)
    if (number === null | number === '' | number === undefined) {
      console.log('addContactcontact2:', number)
      return
    }
    // this.$toast('' + number)
    // 
    if (number.toString().match('[0-9]{1,}')) {
      this.number = number
    } else {
      this.number = ''
    }
  },
  methods: {
    getVerifyCode: function () {
      this.isClearInterval = false // interval
      if (this.number.length === 0) {
        this.$toast('')
        return
      }
      if (!this.isVerifyBtnClickable) {
        this.$toast('')
        return
      }
      if (this.contact.split(',').indexOf(this.number, 0) !== -1) {
        this.$toast('!')
        return
      }
      var that = this
      api.getVerifyCode(sessionStorage.getItem('openId'), this.number).then(res => {
        console.log('', res)
        if (res.data.Code !== 0) {
          if (res.data.Code === -3) {
            that.$toast('')
          } else if (res.data.Code === -6) {
            that.$toast('')
          }
          return
        }
        // 
        that.$toast('')
        that.countDown()
        // console.log(':', res)
      }).catch(err => {
        console.log(':', err)
        that.$toast('!')
      })
    },
    verify: function () {
      var that = this
      if (this.number.length === 0) {
        this.$toast('')
        return
      }
      if (this.verifyNumber.length === 0) {
        this.$toast('')
        return
      }
      const loading = this.showLoading('...')
      api.verifyNumber(sessionStorage.getItem('openId'), this.number, this.verifyNumber)
        .then(res => {
          console.log(':', res)
          if (res.data.Code === 0) {
            // that.$toast('')
            if (that.contact.indexOf(that.number, 0) === -1) {
              // 
              var index = that.$route.query.index
              if (index === null | index === '' | index === undefined) {
                that.contact += that.number + ','
                sessionStorage.setItem('contact', that.contact)
                that.$router.push({
                  path: '/settings',
                  query: {
                    getPhoneNumberFromNet: false
                  }
                })
              } else {
                // settings
                var temp = that.contact.split(',')
                var t = ''
                if (parseInt(index) === 0) {
                  t = that.number + ',' + temp[1] + ',' + temp[2]
                } else if (parseInt(index) === 1) {
                  t = temp[0] + ',' + that.number + ',' + temp[2]
                } else if (parseInt(index) === 2) {
                  t = temp[0] + ',' + temp[1] + ',' + that.number
                }
                // this.$toast('t:' + t)
                console.log('t', t)
                that.contact = t
                sessionStorage.setItem('contact', that.contact)
                // that.$alert('sessionStorage:' + that.contact)
                // settingssettings
                that.$router.push({
                  path: '/settings',
                  query: {
                    getPhoneNumberFromNet: false
                  }
                })
              }
            } else {
              that.$router.push({
                path: '/settings',
                query: {
                  getPhoneNumberFromNet: false
                }
              })
            }
          } else {
            // console.log(':', res.data)
            that.$toast('')
            that.centerDialogVisible = false
          }
        })
        .catch(() => {
          that.$toast('')
          // console.log(':', err)
          that.centerDialogVisible = false
        })
        .finally(() => {
          loading.close()
        })
    },
    countDown: function () {
      var that = this
      that.isVerifyBtnClickable = false
      var count = 60
      var cd = setInterval(function () {
        that.verifyBtnContent = count + 's'
        count--
        // console.log(':', count)
        if (count <= 0 || that.isClearInterval) {
          window.clearInterval(cd)
          that.isVerifyBtnClickable = true
          that.verifyBtnContent = ''
        }
      }, 1000)
    },
    dialogSkip: function () {
      // this.centerDialogVisible = false
      // 
      this.contact = this.contact.substr(0, this.contact.length - 1)
      sessionStorage.setItem('contact', this.contact)
      console.log('')
      this.$router.push({
        path: '/settings',
        query: {
          getPhoneNumberFromNet: false
        }
      })
    },
    dialogAddMore: function () {
      console.log('')
      this.centerDialogVisible = false
      this.isClearInterval = true // interval
      this.number = ''
      this.verifyNumber = ''
    },
    showLoading: function (text) {
      const loading = this.$loading({
        lock: true,
        customClass: 'create-isLoading', //  *class,
        text: text,
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0)'
      })
      return loading
    },
    hideLoading: function (loading) {
      loading.close()
    }
  }
}
</script>

<style scoped>
  .all-container{
    width: 100%;
    height: fit-content;
  }
  .add-tip {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 6rem;
    width: fit-content;
  }
  .linear-line {
    width: 0.3rem;
    height: 50%;
    background-color: #1AAD19;
    border-radius: 0.2rem;
  }
  .margin-horizontal{
    margin-left: 3vw;
    margin-right: 3vw;
  }
  .tip-text{
    font-family: PingFangSC-Regular;
    font-size: 1.9rem;
    color: #666666;
    letter-spacing: 0;
  }
  .contact-panel{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    background-color:#FFFFFF ;
    width: 100%;
    height: fit-content;
  }
  .first-item{
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 5rem;
  }
  .red-star{
    color: red;
    font-size: 1.5rem;
  }
  .title-name{
    font-family: PingFangSC-Regular;
    font-size: 2rem;
    color: #48534A;
    letter-spacing: 0;
  }
  .title-tip{
    font-family: PingFangSC-Regular;
    font-size: 2rem;
    color: #B2B2B2;
    letter-spacing: 0;
  }
  .normal-input-style{
    font-family: PingFangSC-Regular;
    font-size: 2rem;
    color: #B2B2B2;
    letter-spacing: 0;
  }
  /deep/ .no-border-input .el-input__inner{
    border: 0 none;
    color: #48534A;
    max-lines: 1;
    padding: 0;
    font-size: 2rem;
  }
  .first-input-container{
    width: fit-content;
    margin-left: 3vw;
    padding-right: 4vw;
    height: 5rem;
    width: 97%;
  }
  .bottom-border{
    border-bottom: 0.05rem inset #E5E5E5;
  }
  /deep/ .no-background-color{
    background-color: white;
  }
  .second-input-container{
    width: 100%;
    margin-left: 3vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 5rem;
  }
  .sixty-percent{
    width: 60%;
  }
  .fourty-percent{
    width: 40%;
  }
  .verify-code-text-btn{
    font-size: 1.8rem;
    font-family: PingFangSC-Regular;
    color: #1AAD19;
    letter-spacing: 0;
  }
  .wide-button {
    width: 80%;
    margin-top: 20rem;
    margin-bottom: 10rem;
    letter-spacing: 0.5rem;
    font-size: 2rem;
    background-color: #1AAD19;
}

  /*dialog style*/
  .center-dialog-content{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 1.6rem;
  }
  .dialog-btn-group{
    width: 100%;
    height: 5rem;
    display: flex;
    flex-direction: row;
    justify-content:space-evenly;
    align-items: center;
    border-top: 0.05rem inset #E5E5E5;

  }
  .skip-btn{
    font-size: 1.8rem;
    color: #000;
    width: 50%;
    border-right: 0.1rem inset #E5E5E5;
    font-family: PingFangSC-Regular;
    text-align: center;
  }
  .add-another-btn{
    font-size: 1.8rem;
    width: 50%;
    font-family: PingFangSC-Regular;
    color: #02BB00;
    text-align: center;
  }
  /deep/ .el-dialog__footer{
    padding: 0;
  }
</style>



