  <div>
    <div >
      <Topbar class="topbar" :show-save-btn="true" @saveInfo="saveInfo"/>
      <el-row class="alert-row margin-horizontal">!</el-row>
    </div>
    <div class="item-title">
      <span class="title-text">/</span>
      <!--<i class="el-icon-arrow-right big-font-size"/>-->
    </div>
    <!---->
    <!--<div class="contact-panel" v-for="(item,index) in contact" v-bind:key="index">-->
    <div class="contact-panel">
      <el-row class="contact-item border-bottom">
        <span class="contact-name">1</span>
        <span class="contact-number">{{contact[0]=== undefined||contact[0]=== "undefined"||contact[0]=== null||contact[0]=== ""?text1:contact[0]}}</span>
        <span class="delete-img"/>
      </el-row>
      <el-row class="contact-item border-bottom">
        <span class="contact-name">2</span>
        <span class="contact-number" @click="modifyContact(1)">{{contact[1]=== undefined||contact[1]=== "undefined"||contact[1]=== null||contact[1]=== ""?text1:contact[1]}}</span>
        <el-popconfirm
          confirm-button-text=""
          cancel-button-text=""
          icon="el-icon-info"
          iconColor="red"
          @onConfirm="deleteContact(1)"
          title="">
          <i slot="reference" class="el-icon-delete delete-img" v-show="showContact2Delete" />
        </el-popconfirm>
      </el-row>
      <el-row class="contact-item">
        <span class="contact-name">3</span>
        <span class="contact-number" @click="modifyContact(2)">{{contact[2]=== undefined||contact[2]=== "undefined"||contact[2]=== null||contact[2]=== ""?text1:contact[2]}}</span>
        <el-popconfirm
          confirm-button-text=""
          cancel-button-text=""
          icon="el-icon-info"
          iconColor="red"
          @onConfirm="deleteContact(2)"
          title="">
          <i slot="reference" class="el-icon-delete delete-img" v-show="showContact3Delete" />
        </el-popconfirm>
      </el-row>
    </div>
    <!--</div>-->
    <!---->
    <div class="item-title border-bottom">
      <span class="title-text"></span>
     </div>
    <div class="info-fill-panel leftpadding">
      <el-row class="info-item border-bottom ">
        <span class="info-name">*</span>
        <el-input v-model="manInfo.name" maxlength=10 class="info-content normal-input-style " placeholder="()" clearable></el-input>
      </el-row>
      <el-row class="info-item border-bottom ">
        <span class="info-name"></span>
        <el-input v-model="manInfo.address" maxlength=30 class="info-content normal-input-style " placeholder="" clearable></el-input>
      </el-row>
    </div>
      <el-row class="info-item border-bottom leftpadding white-bg">
        <span class="info-name"></span>
        <el-date-picker  type="year" default-value="2020" v-model="manInfo.age" format="yyyy" value-format="yyyy"  class="info-content normal-input-style " placeholder="" ></el-date-picker>
      </el-row>
    <div class="info-fill-panel leftpadding">
      <el-row  class="info-item-height-wrap-content border-bottom">
        <span class="info-name"></span>
        <el-select v-model="manInfo.medical_history"   allow-create filterable multiple  class="info-content normal-input-style " placeholder="" >
          <el-option
            v-for="d in diseases"
            :key="d"
            :label="d"
            :value="d"
          >
          </el-option>
        </el-select>
      </el-row>
      <el-row class="info-item border-bottom">
        <span class="info-name"></span>
        <el-input v-model="manInfo.allergy"  class="info-content normal-input-style " placeholder="" clearable></el-input>
      </el-row>
    </div>

      <el-row class="info-item border-bottom leftpadding white-bg">
        <span class="info-name"></span>
        <el-select v-model="manInfo.blood_type" class="info-content normal-input-style " placeholder="" clearable>
          <el-option
            v-for="b in blood_select"
            :key="b"
            :label="b"
            :value="b"
          >
          </el-option>
        </el-select>
      </el-row>
    <div class="info-fill-panel leftpadding ">
      <el-row class="info-item-height-wrap-content border-bottom">
        <span class="long-info-name"></span>
        <el-input v-model="manInfo.drugs" maxlength=30 type="textarea" autosize class="long-info-content normal-input-style" placeholder="" clearable></el-input>
      </el-row>
      <el-row class="info-item-height-wrap-content">
        <span class="long-info-name"></span>
        <el-input v-model="manInfo.treatment" maxlength=30 type="textarea" autosize class="long-info-content normal-input-style" placeholder="" clearable></el-input>
      </el-row>
    </div>
    <el-button type="success" class="wide-button" @click="saveInfo"></el-button>
    <el-dialog
      :lock-scroll="false"
      :visible.sync="centerDialogVisible"
      title=""
