      :show-close="false"
      center>
      <span class="center-dialog-content">!</span>
      <span slot="footer" class="dialog-footer dialog-btn-group">
        <span type="primary" @click="dialogConfirm" class="confirm-btn"></span>
      </span>
    </el-dialog>
