<template>
  <div class="all-container">
    <topbar :hide-back-btn="true"/>
    <div class="img-text-container">
<!--      <div class="text-container">-->
<!--        <label v-html="this.tip" class="label-style"></label>-->
<!--      </div>-->
      <el-image :src="img_url" class="img-style"></el-image>
    </div>
    <el-button type="success" class="wide-button" v-on:click.prevent="routeToScanSuccess"></el-button>
    <el-dialog
      :lock-scroll="false"
      :visible.sync="showVerifyDialog"
      title=""
      width="80%"
      :show-close="false"
      :modal="true"
      center>
      <span class="dialog-info">,</span>
      <el-input placeholder="" v-model="phoneNumber"></el-input>
      <el-button @click="verify"></el-button>
    </el-dialog>
  </div>
</template>

<script>
import topbar from './topbar'
import wx from 'weixin-js-sdk'
import api from '../api'
export default {
  name: 'scan',
  components: {topbar},
  data () {
    return {
      phoneNumber: '',
      showVerifyDialog: false,
      tip: '<span class="black-text"></span>',
      img_url: require('../assets/yellow_card.png')
    }
  },
  created () {
    document.title = this.$route.meta.title
  },
  mounted () {
    console.log('created---')
    wx.ready(function () {
      console.log('ready-----')
    })
    wx.error(function (res) {
      console.log('error-----')
    })
    const loading = this.showLoading('...')
    api.wxConfig().then(config => {
      wx.config({
        debug: false,
        appId: config.data.appId,
        timestamp: config.data.timestamp,
        nonceStr: config.data.nonceStr,
        signature: config.data.signature,
        jsApiList: ['scanQRCode']
      })
    }).finally(() => {
      this.hideLoading(loading)
    })
    // 
    sessionStorage.removeItem('manInfo')
    sessionStorage.removeItem('contact')
    sessionStorage.removeItem('qrCodeId')
    const loading2 = this.showLoading('...')
    setTimeout(() => {
      this.hideLoading(loading2)
    }, 1000)
  },
  methods: {
    verify: function () {
      if (!this.phoneNumber) {
        this.$toast('!')
        return
      }
      let qrCodeId = sessionStorage.getItem('qrCodeId')
      api.isFamilyMember(qrCodeId, this.phoneNumber)
        .then(({data: {status_code: statusCode, isFamilyMember}}) => {
          console.log('status_code, isFamilyMember', statusCode, isFamilyMember)
          if (statusCode === 0) {
            if (isFamilyMember) {
              // 
              api.updateQrcodeOwner(qrCodeId)
                .then(({data: {Code, Message}}) => {
                  console.log(':', Code, Message)
                  if (Code === 0) {
                    // 
                    console.log('!')
                    this.$router.push('/settings')
                  }
                })
            } else {
              console.log('!')
            }
          } else {
            console.log('!')
          }
        }).catch(err => {
          console.log(':', err)
      })
    },
    routeToScanSuccess: function () {
      try {
        this.startScan()
      } catch (e) {
        this.$toast(',' + e)
      }
    },
    startScan: function () {
      var that = this
      var ucallFreeId = sessionStorage.getItem('UcallFreeId')
      if (ucallFreeId === null || ucallFreeId === '' || ucallFreeId === 'undefined' || ucallFreeId === undefined) {
        return
      }
      wx.scanQRCode({
        needResult: 1, // 01
        scanType: ['qrCode', 'barCode'], // 
        success: function (res) {
          var result = res.resultStr // needResult  1 
          that.handleScanResult(result)
          console.log('scanQrCode:', result)
        }
      })
    },
    handleScanResult: function (content) {
      var that = this
      if (content.indexOf('qrcodeid') !== -1) { // urlqrcodeid
        var substr = content.split('?')[1]
        // var params = substr.split('&')
        var qrcodeid = this.getQueryValue(substr, 'qrcodeid')
        console.log('qrcode:' + qrcodeid)
        sessionStorage.setItem('qrCodeId', qrcodeid)
        api.verifyQrCodeActive(qrcodeid).then(res => {
          console.log(':', res)
          var UcallFreeId = sessionStorage.getItem('UcallFreeId')
          console.log('UCallfreeid:', UcallFreeId)
          var data = res.data
          if (data.Code === 0) {
            switch (data.ActiveState) {
              case 0:
                // 
                sessionStorage.setItem('isQrCodeActive', 0)
                if (sessionStorage.getItem('from') === 'admin') {
                  // 
                  this.$router.push('/settingsForAdmin')
                } else {
                  that.$router.push('/addContact')
                }
                break
              case 1:
                // 
                sessionStorage.setItem('isQrCodeActive', 1)
                // owner
                if (data.Owner === UcallFreeId) {
                  if (sessionStorage.getItem('from') === 'admin') {
                    // 
                    this.$router.push('/settingsForAdmin')
                  }
                  // 
                  that.$router.push('/settings')
                } else {
                  // 
                  // that.$router.push('/alreadyBindInfo')
                  that.showVerifyDialog = true
                }
                break
              default:
                // alert('')
                that.$toast('')
                break
            }
          }
        }).catch(res => {
          console.log('error:', res)
          this.$toast('')
        })
      }
    },
    getQueryValue: function (str, queryName) {
      var vars = str.split('&')
      for (var i = 0; i < vars.length; i++) {
        var pair = vars[i].split('=')
        if (pair[0] === queryName) {
          return pair[1]
        }
      }
      return null
    },
    showLoading: function (text) {
      const loading = this.$loading({
        lock: true,
        customClass: 'create-isLoading', //  *class,
        text: text,
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0)'
      })
      return loading
    },
    hideLoading: function (loading) {
      loading.close()
    }
  }
}
</script>

<style scoped>
  .all-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .img-text-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 5rem 5rem;
    position: relative;
  }
  .img-style{
  }
  .text-container{
    padding: 10% 5vw;
    position: absolute;
    z-index: 2;
  }
  .dialog-info{
    width: 100%;
    font-size: 1.3rem;
    letter-spacing: 1px;
    font-family: "PingFang SC";
    color: black;
    word-break: break-all;
    text-align: center;
  ;
  }
  .label-style{
    font-size:1.5rem;
    font-family: "PingFang SC";
    color: #666666;
    letter-spacing: 0.01rem;
  }
  /deep/ .black-text{
    color: #121212;
    font-size:1.5rem;
    font-family: "PingFang SC";
    letter-spacing: 0.01rem;
    font-style: normal;
    font-weight: bold;
  }
  .wide-button{
    width: 80%;
    height: 5rem;
    margin-top: 3rem;
    margin-bottom: 5rem;
    letter-spacing: 0.5rem;
    font-size: 2rem;
    background-color: #1AAD19;

  }
</style>



